.calendar-demo {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.calendar-demo .rmdp-wrapper {
  box-shadow: unset;
}

@media (min-width: 550px) {
  .selector {
    width: 80%;
  }

  .selector div {
    font-size: 14px;
    height: 24px;
  }
}

@media (max-width: 550px) {
  .selector {
    width: 100%;
  }

  .selector div {
    font-size: 13px;
    height: 20px;
  }
}

@media (max-width: 400px) {
  .selector div {
    font-size: 12px;
    height: 20px;
    padding-bottom: 2px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
}

.selector {
  display: flex;
  margin: auto;
}

.selector-wrapper {
  display: "flex";
  justify-content: "center";
}

.selector-container {
  flex: 1;
  margin-top: 10px;
  text-align: center;
}

.selector-container span {
  border: 1px solid #9cbdda;
  padding: 3px 12px;
  border-radius: 12px;
  font-size: 12px;
  color: #0074d9;
  font-weight: bold;
}

.selector {
  margin-top: 7px;
}

.selector div {
  flex: 1;
  text-align: center;
  border: 1px solid #9cbdda;
  margin: 2px 4px;
  border-radius: 10px;
  transition: 0.3s;
  padding-top: 2px;
  overflow: hidden;
}

.selector div:hover {
  background-color: #4aabff;
  color: white;
  transition: 0.3s;
  cursor: pointer;
}

.selector div.active {
  background-color: #0074d9;
  color: white;
  cursor: pointer;
}

table td,
table th {
  margin: 0;
  padding: 2px 4px;
  font-size: 14px;
}
